@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  font-size: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 40px;
  @include tablet {
    padding-bottom: 60px;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 25px;
  height: 25px;
  padding: 6px 0 4px;
  margin-left: 0;
  margin-right: 7px;
  color: var(--accentBtnColor);
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  border: 0;
  border-width: 1px 0;
  border-radius: 5px;
  cursor: pointer;
  transition: var(--timingFunction);
  @include tablet {
    width: 40px;
    height: 40px;
    padding: 8px 0 6px;
    margin-right: 10px;
    font-size: 14px;
    font-weight: 500;
    line-height: 25px;
  }
}

.tui-pagination .tui-page-btn:hover,
.tui-pagination .tui-page-btn:focus {
  background-color: var(--accentBtnColor);
  color: var(--secondaryLightThemeBgColor);
  transform: scale(1.15);
  box-shadow: rgba(136, 165, 191, 0.48) 2px 1px 6px 0px,
    rgba(255, 255, 255, 0.8) -2px -1px 6px 0px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: var(--secondaryLightThemeBgColor);
  background: var(--accentBtnColor);
  box-shadow: rgba(136, 165, 191, 0.48) 2px 1px 6px 0px,
    rgba(255, 255, 255, 0.8) -2px -1px 6px 0px;
  border: 0;
  cursor: default;
  transition: var(--timingFunction);
}

.tui-pagination .tui-is-selected:hover,
.tui-pagination .tui-is-selected:focus {
  background-color: var(--accentBtnColor);
  color: var(--secondaryLightThemeBgColor);
  transform: scale(1.15);
  box-shadow: rgba(136, 165, 191, 0.48) 2px 1px 6px 0px,
    rgba(255, 255, 255, 0.8) -2px -1px 6px 0px;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border: 1px solid var(--accentBtnColor);
  background-color: var(--accentBtnColor);
  color: var(--secondaryLightThemeBgColor);
  transition: var(--timingFunction);

  &:hover,
  &:focus {
    background-color: var(--accentBtnColor);
    color: var(--secondaryLightThemeBgColor);
    transform: scale(1.15);
    box-shadow: rgba(136, 165, 191, 0.48) 2px 1px 6px 0px,
      rgba(255, 255, 255, 0.8) -2px -1px 6px 0px;
  }
}

.tui-pagination .tui-last-child.tui-is-selected {
  border: 1px solid var(--accentBtnColor);
  background-color: var(--accentBtnColor);
  color: var(--secondaryLightThemeBgColor);
  transition: var(--timingFunction);
  &:hover,
  &:focus {
    background-color: var(--accentBtnColor);
    color: var(--secondaryLightThemeBgColor);
    transform: scale(1.15);
    box-shadow: rgba(136, 165, 191, 0.48) 2px 1px 6px 0px,
      rgba(255, 255, 255, 0.8) -2px -1px 6px 0px;
  }
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  overflow: hidden;
  height: 30px;
  padding: 0;
  font-size: 0;
  line-height: 28px;
  white-space: nowrap;
  font-weight: 500;
  border: 0;
  background-color: #f7f7f7;
}
.tui-pagination .tui-first,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 0;
  padding: 0;
  font-size: 0;
  line-height: 0;
  white-space: nowrap;
  font-weight: 0;
  border: 0;
  @include tablet {
    overflow: hidden;
    height: 30px;
    padding: 0;
    font-size: 0;
    line-height: 28px;
    white-space: nowrap;
    font-weight: 500;
    border: 0;
    background-color: #f7f7f7;
  }
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 1px;
  @include tablet {
    margin-right: 8px;
  }
}

.tui-pagination .tui-ico-first {
  width: 1px;
  height: 1px;

  @include tablet {
    width: 11px;
    height: 10px;
    background-position: 0px 0px;
  }
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 1px;
  height: 1px;

  @include tablet {
    width: 11px;
    height: 10px;
    background-position: 0px -12px;
  }
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  color: var(--secondaryTextColor);
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #e6554b;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
