.trailer__icon-youtube {
  margin-left: 10px;
  margin-right: 10px;
}

.trailer__icon-youtube:hover, .trailer__icon-youtube:focus {
  fill: #000;
}

.trailer__button {
  width: 220px;
  height: 48px;
  border: 2px solid var(--secondaryTextColor);
  color: var(--secondaryTextColor);
  cursor: pointer;
  z-index: 0;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  position: relative;
}

.trailer__button:hover, .trailer__button:focus {
  color: #000;
}

.trailer__button:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  opacity: 0;
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 10px;
  transition: opacity .3s ease-in-out;
  animation: glowing 20s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

.trailer__button:active {
  color: #000;
  background: none;
}

.trailer__button:active:after {
  background: none;
}

.trailer__button:hover:before {
  opacity: 1;
}

.trailer__button:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  filter: blur(5px);
  background: none;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

